.pc {
  display: block;
}

.sp {
  display: none;
}

p {
  line-break: normal;
  line-height: 2em;
}

@media only screen and (width <= 768px) {
  .pc {
    display: none;
  }

  .sp {
    display: block;
  }
}

.screen textarea:focus, .screen input:focus {
  outline: none;
}

.screen * {
  -webkit-font-smoothing: antialiased;
  box-sizing: border-box;
}

.screen div {
  -webkit-text-size-adjust: none;
}

.screen a {
  text-decoration: none;
  display: contents;
}

.full-width-a {
  width: 100%;
}

.full-height-a {
  height: 100%;
}

@keyframes reveal {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

